import React from "react";
import { Link } from "react-router-dom";
import htmlicon from "./images/htmlicon.svg";
import cssicon from "./images/cssicon.svg";
import apiicon from "./images/apiicon.svg";
import giticon from "./images/giticon.svg";
import reacticon from "./images/reacticon.svg";
import javascripticon from "./images/javascripticon.svg";
import vscodeicon from "./images/vscodeicon.svg";
import tailwindicon from "./images/tailwindicon.svg";
import sassicon from "./images/sassicon.svg";
import bootstrapicon from "./images/bootstrapicon.svg";
import npmicon from "./images/npmicon.svg";
import jqueryicon from "./images/jqueryicon.svg";

const skillImg = [
  {
    img: htmlicon,
    link: "https://www.google.com/search?q=what+is+html+&rlz=1C1ONGR_itIT1028IT1028&sxsrf=ALiCzsZBg7cS5t3x566s8hFxkwkUtkRkyQ%3A1666287844253&ei=5IhRY8_9DoOllwTS1YuADw&ved=0ahUKEwjP_7L4re_6AhWD0oUKHdLqAvAQ4dUDCA8&uact=5&oq=what+is+html+&gs_lp=Egxnd3Mtd2l6LXNlcnC4AQP4AQEaAhgDMgcQIxjqAhgnMgcQIxjqAhgnMgcQIxjqAhgnMgcQIxjqAhgnMgcQIxjqAhgnMgcQIxjqAhgnMgcQIxjqAhgnMgcQIxjqAhgnMgcQIxjqAhgnMhAQLhjHARjRAxjqAhgnGIsDwgIEECMYJ8ICBBAAGEOoAgpI6EZQ-xVYlkVwBXgByAEAkAEBmAHRCqABsCiqAQc1LTEuMS4z4gMEIE0YAeIDBCBBGADiAwQgRhgAiAYB&sclient=gws-wiz-serp",
  },
  { img: cssicon, link: "https://developer.mozilla.org/en-US/docs/Web/CSS" },
  {
    img: javascripticon,
    link: "https://www.google.com/search?q=javascript&rlz=1C1ONGR_itIT1028IT1028&oq=javascript+&aqs=chrome..69i57.3910j0j7&sourceid=chrome&ie=UTF-8",
  },
  { img: vscodeicon, link: "https://code.visualstudio.com/" },
  {
    img: tailwindicon,
    link: "https://www.google.com/search?q=tailwind+css&rlz=1C1ONGR_itIT1028IT1028&oq=tailwind+css&aqs=chrome..69i57j0i271l3j69i60j69i61j69i60.2749j0j7&sourceid=chrome&ie=UTF-8",
  },
  {
    img: reacticon,
    link: "https://www.google.com/search?q=react&rlz=1C1ONGR_itIT1028IT1028&oq=react&aqs=chrome..69i57j0i271l3j69i60j69i61l2j69i60.13391j0j7&sourceid=chrome&ie=UTF-8",
  },
  {
    img: apiicon,
    link: "https://www.techtarget.com/searchapparchitecture/definition/application-program-interface-API",
  },
  {
    img: giticon,
    link: "https://www.google.com/search?q=git&rlz=1C1ONGR_itIT1028IT1028&oq=git&aqs=chrome..69i57j69i59j0i271l2j69i60l4.1995j0j7&sourceid=chrome&ie=UTF-8",
  },
  {
    img: sassicon,
    link: "https://www.google.com/search?q=sass&source=lmns&rlz=1C1ONGR_itIT1028IT1028&hl=en&sa=X&ved=2ahUKEwi06ZLsu-_6AhUlrEwKHeodA_wQ_AUoAHoECAEQAA",
  },
  {
    img: bootstrapicon,
    link: "https://getbootstrap.com/",
  },
  {
    img: npmicon,
    link: "https://www.npmjs.com/",
  },
  {
    img: jqueryicon,
    link: "https://jquery.com/",
  },
];
function About() {
  return (
    <section className="pb-10">
      <h1 className="text-center text-[#E3E3E3] dark:text-[#1E1E1E]  text-5xl pb-6 mt-28 font-bold tracking-wider ">
        About me
      </h1>
      <div className="w-[30px] h-[8px] bg-[#9499ff] rounded-[10px] mx-auto"></div>
      <h2 className="text-center text-[#E3E3E3] dark:text-[#1E1E1E]  text-xl mt-5 font-semibold tracking-wider">
        Hi there, I'm Daniel
      </h2>
      <div className="  text-center text-[#E3E3E3]   leading-7 dark:text-[#1E1E1E] mt-16 mx-12 pt-7 md:mx-26 lg:mx-36 xl:mx-[20rem] lg:text-xl lg:leading-[2rem] ">
        <p>
          I am a front-end developer with previous{" "}
          <span className="text-[#9499ff]">
            <Link className="hover:underline" to="/projects">
              working experience
            </Link>
          </span>{" "}
          in creating complex ux design. I enjoy the challenge of{" "}
          <span className="text-[#9499ff]">creating complex applications</span>{" "}
          and experiences that are{" "}
          <span className="text-[#9499ff]">
            interactive, informative and accessible
          </span>
          . I strive to learn and improve my skills on the latest {""}
          <span className="text-[#8499ff]">
            <a
              className="hover:underline"
              target="_blank"
              rel="noreferrer"
              href="https://www.geeksforgeeks.org/web-technology/#:~:text=Web%20Technology%20refers%20to%20the,and%20video%20on%20the%20Internet."
            >
              web technologies
            </a>
          </span>
          , and challenge myself every day to become a more knowledgeable and
          effective developer.
        </p>
      </div>

      <div className="grid justify-center mx-3 pt-7  ">
        <h1 className="text-center text-[#E3E3E3] dark:text-[#1E1E1E]  text-5xl pb-6 mt-28 font-bold tracking-wider ">
          Tools
        </h1>
        <div className="w-[30px] h-[8px] bg-[#9499ff] rounded-[10px] mx-auto"></div>
        <h2 className="text-center text-[#E3E3E3] dark:text-[#1E1E1E]  text-xl mt-6 font-semibold tracking-wider">
          Main tools I use for my projects
        </h2>
        <div className="grid justify-center grid-cols-3 md:grid-cols-4 gap-6 lg:gap-20 mt-16">
          {skillImg.map((skills, i) => {
            const { img, link } = skills;
            const key = i;

            return (
              <button
                key={key}
                onClick={() => {}}
                className=" flex justify-center duration-500 transition-all  bg-[#161616] hover:bg-[#2c2b2b] dark:bg-gray-200 dark:hover:bg-[#ffffff] shadow-lg  w-[6rem] py-6 rounded-md "
              >
                <div className="flex flex-col justify-center items-center  max-w-[4rem] w-12">
                  <a
                    href={link}
                    target="_blank"
                    rel="noreferrer"
                    className="w-full"
                  >
                    <img src={img} className="w-full" alt="" />
                  </a>
                </div>
              </button>
            );
          })}
        </div>
      </div>
    </section>
  );
}

export default About;
